.frame {
    background-size: contain;
    display: inline-block;
    height: calc(100%em);
    position: relative;
}

.course-title {
    font-family: "Secular One";
    margin-top: 5%;
    margin-left: 5%;
}

.links {
    margin-left: 5%;
    margin-bottom: 5%;
    word-wrap: break-word;
}

/* 
   * Positions edit button at bottom center of container 
   * The translate() adjusts for the edit button's width and height
   */
.edit-btn {
    bottom: 0;
    left: 50%;
    position: absolute;
    transform: translate(-50%, -50%);
}